<template>
    <td class="vue-advanced-table-cell" v-if="isColumnVisible(column.name)">
      <slot>{{ content }}</slot>
    </td>
</template>

<script>
export default {
  name: 'vue-advanced-table-cell',
  props: {
    rows: {
      type: Array,
      required: true
    },
    columns: {
      type: Array,
      required: true
    },
    hiddenColumns: {
      type: Array,
      required: true
    },
    row: {
      type: Object,
      required: true
    },
    column: {
      type: Object,
      required: true
    }
  },
  mounted: function() {
  },
  methods: {
    getColumnByName: function(name) {
      const self = this;
      return self.columns.find(function(column) {
        return column.name === name;
      });
    },
    isColumnVisible: function(column) {
      const self = this;
      return self.hiddenColumns.indexOf(column) === -1
    }
  },
  computed: {
    content: function() {
      const self = this;
      const column = self.getColumnByName(self.column.name);
      if (typeof column.render === 'function') {
        return column.render(self.row[column.name]);
      }

      return self.row[column.name];
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .vue-advanced-table-cell {
    padding: 4px;
  }
</style>
